<template>
<div class="container" style="padding-top:30px;padding-bottom:30px;" id="about">
  <el-row :gutter="30">
    <el-col :md="12">
        <h2 class='title'>关于我们</h2>
        <p>南京邮电大学理学院科学技术协会（Science Association of Science College)，简称SASC，中文简称南邮理学院科协，成立于1999年。它是在南邮理学院分团委指导下，开展学生科技文化及学术研究的组织。成立以来，科协人不断进取，众多成员被知名企业聘用或顶尖高校录取。</p>
        <img src="../assets/banners/all_new.jpeg" alt="全体成员">

    </el-col>
    <el-col :md="12">
        <h2 class='title'>我们一般做什么？<a href=""></a></h2> 
        <p>这里是我们科协最新的动态资讯和活动公告.</p>
          <ol class="rounded-list">
            <li  v-for="news in all_news" key="news.id">
              <a href="">{{news.title}} | {{news.publish_date}}</a>
            </li>                    
        </ol>
    </el-col>
</el-row>
</div>
</template>

<script >
export default {
  data() {
    return {
      all_news: [],
    };
  },
  mounted() {
    this.axios.get('api/news/').then((response) => {
      // response.data.forEach((news) => {

      // });
      this.all_news = response.data;
    });
  },
};
</script>

<style lang='less' scoped>
#about{
  background-color: white;
}

.news{
    list-style-type : None;
    .title{
      text-align: center
    }
  a{
      color: white;
      text-decoration: none; /* no underline */
  }  
}


ol{
  counter-reset: li; /* 创建一个计数器 */
  list-style: none; /* 清除列表默认的编码*/
  *list-style: decimal; /* 让IE6/7具有默认的编码 */
  padding: 0;
  margin-bottom: 4em;
  text-shadow: 0 1px 0 rgba(255,255,255,.5);
}
  
.rounded-list a{
    position: relative;
    display: block;
    padding: .4em .4em .4em 2em;
    *padding: .4em;
    margin: .5em 0;
    background: #ddd;
    color: #444;
    text-decoration: none;
    border-radius: .3em;
    transition: all .3s ease-out;   
}

.rounded-list a:hover{
    background: #eee;
}

.rounded-list a:hover:before{
    transform: rotate(360deg);  
}

.rounded-list a:before{
    content: counter(li);
    counter-increment: li;
    position: absolute; 
    left: -1.3em;
    top: 50%;
    margin-top: -1.3em;
    background: #87ceeb;
    height: 2em;
    width: 2em;
    line-height: 2em;
    border: .3em solid #fff;
    text-align: center;
    font-weight: bold;
    border-radius: 2em;
    transition: all .3s ease-out;
}
img{
object-fit: cover;
height:206px; 
width:100%;
border-radius:15px;
}
</style>



