                <p>中国 江苏省 南京市栖霞区文苑路9号<br>南京邮电大学 教2-501<br> 理学院科学技术协会.<br>
                网站（Website）：
                <a href="https://www.nuptsasc.com" id="our_url">https://www.nuptsasc.com</a>
                <br>电子邮箱（Email）: <a href="mailto:contact@nuptsasc.com">contact@nuptsasc.com</a></p>
            </div>
          </el-col>
          <el-col :sm="8" >
            <div class="section" style="text-align:center">
              <h2 class="section-title" id="contact_us">QQ群(群号:581563416)</h2>
              <img src="../assets/images/qr_qq.gif" alt="" style="width:200px; height200px">
              <br>
                <a target="_blank" href="//shang.qq.com/wpa/qunwpa?idkey=545494726910e7f7fdbb90b3b8b3538928adabe53e45cbf356a1a2ab67a94cfa"><img border="0" src="//pub.idqqimg.com/wpa/images/group.png" alt="SASC.2017新生群" title="SASC.2017新生群"></a>
            </div>
