          </el-col>
        </el-row>
      </div>
    </div>
    

</div>
</template>

<script>
export default {
  data() {
    return {
      center: [118.930934, 32.107662],
      scrollWheel: false,
      zoom: 15,
      componentMarker: {
        position: [118.930934, 32.107662],
      },
    };
  },
};
</script>

<style lang='less' scoped>

#wrapper{
    position: relative;
}

.amap-wrapper {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: -1;
}

.img-overlay {
  position: absolute;
  background: url(../assets/pattern.png) repeat, rgba(40, 41, 46, 0.35);
  width: 100%;
  height: inherit;
  z-index: 3;
}

#content{
  color: white;
  z-index: 4;
  width: inherit;   
  a{
    color: white;
      text-decoration: none; /* no underline */
  }         
}

.section{
  text-align: left;
  font-size: 18px;
}
</style>



